
.modalwindow{
    //.modalwindow__container{}
    &__container{

        width: 80%;
        height: 50vh;
        // background-color: #fff;
        display: none;
        z-index: 999999;
        &::after{
            z-index: -999;
            content: '';
            position: absolute;
            left: 0;
            top: 0;
            width: 100vw;
            height: 100vh;
            // background-color: #00000036;
        }

        //.modalwindow__container--show{}
        &--show{
            left: 50%;
            top: 50%;
            transform: translate(-50%,-50%);
            position: absolute;
            display: flex;
            align-items: center;
            justify-content: center;
            // height: min-content;
            // overflow-x: hidden;
            min-height: 100%; //63  
            max-height: 83%;
            height: 100%;
            width: 100%;
            &::before{
                content: '';
                width: 100%;
                height: 89%;
                background-color: #ffffff;
                position: absolute;
                z-index: 1;
                border-radius: 7px;
                box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08);
            }
        }
    }
    //.modalwindow__wrap{}
    &__wrap{
        padding: 15px;
        font-size: 16px;
        position: relative;
        min-height: 100%;
        min-width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        flex-direction: column;
        // max-height: -webkit-fill-available;
        height: 100%;
        width: 100%;

    }
    //.modalwindow__close{}
    &__close{
        z-index: 999999999;
        top: 10px;
        right: 10px;
        position: absolute;
        transform: translate(8px, -9px);
        z-index: 999999999;
        background-color: #c3c3c3;
        width: 34px;
        height: 34px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        color: #fff;
    }
    //.modalwindow__content{}
    &__content{
        // flex: 1 1 auto;
        padding: 10px;
        overflow: auto;
        // display: flex;
        // justify-content: center;
        // flex-direction: column;
        // align-items: center;
        position: relative;
        width: calc(100% + 22px);

    }
    //.modalwindow__control-panel-wrap{}
    &__control-panel-wrap{
        flex: 0 0 70px;
        margin-bottom: 45px;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        position: relative;
    }
    //.modalwindow__button{}
    &__button{
        padding: 12px 35px;
        border-radius: 7px;
        border: #2f2e2e;
        background-color: #000;
        color: #fff;
        text-decoration: U
    }
}