.message{
    //.message__description{}
    &__description{
        // min-height: 43px;
        margin: 0 15px 15px;
        width: calc(100% - 30px);
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        // box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);
        box-shadow: 0 5px 15px rgba(0, 0, 0, .5);
         
        border-radius: 8px;
        min-width: 300px;

        &>div {
            float: left;
        }
    }
}

.form-data-user{
    //.form-data-user__container{}
    &__container{
        padding: 0;
    }
    //.form-data-user__items{}
    &__items{
        padding: 0;
        display: flex;
    }
    //.form-data-user__items-title{}
    &__items-title{
        padding: 0;
        color: #000;
        margin-right: 5px;
    }
    //.form-data-user__items-data{}
    &__items-data{
        padding: 0;
    }
}