.pagination{
    //.pagination__container{}
    &__container{
        width: 100%;
        align-items: flex-end;
        justify-items: center;
        margin: 20px 0;
        max-width: 300px;
        display: flex;
        margin-top: 50px;
        // overflow: hidden;        
    }
    //.pagination__inner-container{}
    &__inner-container{
        display: grid;
        grid-auto-columns: 50px; 
        grid-auto-flow:column;
        grid-template-rows: 1fr;
        justify-content: center;
    }
    //.pagination__item{}
    &__item{
        margin-right: 15px;
        font-size: 14px;
        position: relative;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #777;
        &-active{
            width: 40px;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: var(--text-color);
            font-size: 16px;
            position: relative;
            cursor: not-allowed;
            &::after{
                content: "";
                box-shadow: 0 0px 5px 3px  var(--text-color);
                border-radius: 2px;
                transform: rotate(45deg);
                position: absolute;
                height: 35px;
                left: 3px;
                top: 1px;
                width: 35px;
            }
        }

        &:hover{
            cursor: pointer;
            color: rgb(19, 13, 17);
        }
    }
}