.form-payment-access{
    //.form-payment-access__container{}
    &__container{
        width: 100%;
        min-height: 260px;
        width: calc(100% - 30px);
        padding: 15px;
        position: relative;
        z-index: 11;
        background: rgba(255, 255, 255, 0.8);
        // box-shadow: 0px 12px 24px 6px rgba(97, 97, 97, 0.08), 0px 24px 48px 6px rgba(97, 97, 97, 0.08);
        box-shadow: 0 5px 15px rgba(0, 0, 0, .5);         
        border-radius: 8px;
        margin: 0 auto 10px;
    }
    //.form-payment-access__nav-container{}
    &__nav-container{
        display: grid;
        grid-template-columns: 1fr 1fr;

    }
    //.form-payment-access__nav-button{}
    &__nav-button{
        width: 100%;
        margin-top: 5px;
        padding: 5px 5px 5px 5px;
        border-radius: 7px;
        min-height: 56px;
        display: flex;
        align-items: center;    
        justify-content: center;
        text-align: center;
        color: #5B75B2;
        font-size: 14px;
        font-weight: 900;
        //.form-payment-access__nav-button--active{}
        &--active{
            background-color: #004CFF;
            color: #becbeb;
        }
    }
    //.form-payment-access__discription{}
    &__discription{
        margin-top: 20px;
        margin-bottom: 20px;
    }
    //.form-payment-access__discription-wrap{}
    &__discription-wrap{
        padding: 0;
    }
    //.form-payment-access__discription-title{}
    &__discription-title{
        font-weight: 900;
        font-size: 16px;
        line-height: 120%;        
    }
    //.form-payment-access__discription-context-list{}
    &__discription-context-list{
        padding: 0;
        margin-top: 15px;
    }
    //.form-payment-access__discription-context-item{}
    &__discription-context-item{
        display: flex;
        align-items: baseline;
        &>div:nth-child(1){
            transform: translate(-4px, 5px);
        }
    }
    //.form-payment-access__discription-context-item-title{}
    &__discription-context-item-title{
        font-weight: 900;
        font-size: 12px;
        margin-right: 20px;
        line-height: 240%;   
        min-width: fit-content;
    }
    //.form-payment-access__discription-context-item-desc{}
    &__discription-context-item-desc{
        padding: 0;
        position: relative;
        &::before{
            content: '';
            width: 10px;
            height: 1px;
            position: absolute;
            background-color: #000000ff;
            top: 8px;
            left: -13px;
        }
    }
}